.btn {
  font-family: "Untitled Sans";
  color: #d7d9dd;
  position: relative;
  background: none;
  border: none;
  font-size: 1.1rem;
  padding: 0;
  transform-origin: left;
  -webkit-transition: all 0.75s ease;
  -moz-transition: all 0.75s ease;
  -ms-transition: all 0.75s ease;
  -o-transition: all 0.75s ease;
  transition: all 0.75s ease;
}
.btn::before {
  content: "";
  height: 1px;
  background-color: #4f4f51;
  width: 100%;
  transform: scaleX(0);
  transition: all 0.25s ease;
  position: absolute;
  bottom: 0;
  transform-origin: left;
}
.btn:hover {
  cursor: pointer;
  color: #4f4f51;
}
.btn:hover::before {
  transform: scaleX(1);
}
.btn:hover,
.btn:active,
.btn:focus {
  cursor: pointer;
  color: #4f4f51;
  outline: none;
}
.btn:hover::before,
.btn:active::before,
.btn:focus::before {
  transform: scaleX(1);
}

